<template id="msg">
  <transition name="fade">
    <div
      class="msg_wrap fixed flex justify_center align_center"
      v-if="visible_msg"
    >
      <span class="msg">{{ msg }}</span>
    </div>
  </transition>
</template>
<script>
export default {
  props: {
    msg: {
      type: String,
      default: "",
    },
    visible_msg: {
      type: Boolean,
      default: false,
    },
  },
};
</script>
<style lang="scss">
.msg_wrap {
  bottom: 24px;
  right: 24px;
  height: 48px;
  padding: 0 24px;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.9);
  color: white;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  font-size: 13px;
  line-height: 1;
  z-index: 10000;
}
</style>
