<template id="imgitems">
  <div>
    <Msg :visible_msg="visible_msg" :msg="msg"></Msg>
    <template id="detail">
      <transition name="fade">
        <div
          id="detail_wrap"
          class="detail_wrap flex justify_center align_center fixed w_100vw h_100vh top_0 left_0 z_index_1000"
          v-if="current !== -1"
          @click="onImgItem"
          @mousewheel.stop.prevent=""
          @touchmove.stop.prevent=""
        >
          <div :class="['detail relative', style]">
            <div
              class="img_item transition_normal"
              :style="current_data[current].download_url | getStyles"
            >
              <button
                class="btn_close border_box flex justify_center align_center absolute"
              >
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                  <path
                    fill="none"
                    stroke="currentColor"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="32"
                    d="M368 368L144 144M368 144L144 368"
                  />
                </svg>
              </button>
              <div
                class="detail_options style border_box flex justify_center align_center absolute"
                @click.stop="onChooseStyle"
              >
                <button
                  :class="style === 'contain' ? 'active' : ''"
                  data-style="contain"
                >
                  contain
                </button>
                <button
                  :class="style === 'cover' ? 'active' : ''"
                  data-style="cover"
                >
                  cover
                </button>
              </div>
              <a
                class="btn_download absolute border_box flex justify_center align_center transition_normal"
                :href="current_data[current].download_url"
                :download="current_data[current].name"
                @click.prevent="download(current_data[current].download_url)"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 512 512"
                  width="15"
                  height="15"
                >
                  <path
                    fill="none"
                    stroke="currentColor"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="48"
                    d="M112 268l144 144 144-144M256 392V100"
                  />
                </svg>
              </a>
              <button
                :class="['btn_autoplay absolute', autoplay ? 'playing' : '']"
                @click.stop="onToggleAutoPlay"
              >
                {{ autoplay ? "stop play" : "autoplay" }}
              </button>
              <div
                class="detail_options current border_box flex justify_center align_center absolute"
                @click.stop="onChangeCurrent"
              >
                <button
                  :class="Number(current) <= 0 ? 'disabled' : ''"
                  data-type="prev"
                >
                  prev
                </button>
                <button
                  :class="
                    Number(current) + 1 >= current_data.length ? 'disabled' : ''
                  "
                  data-type="next"
                >
                  next
                </button>
              </div>
            </div>
          </div>
        </div>
      </transition>
    </template>
    <template id="empty">
      <div
        class="empty_wrap flex flex_column justify_center align_center"
        v-if="!current_data.length"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 512 512"
          width="72"
          height="72"
        >
          <rect
            x="48"
            y="80"
            width="416"
            height="352"
            rx="48"
            ry="48"
            fill="none"
            stroke="currentColor"
            stroke-linejoin="round"
            stroke-width="32"
          />
          <circle
            cx="336"
            cy="176"
            r="32"
            fill="none"
            stroke="currentColor"
            stroke-miterlimit="10"
            stroke-width="32"
          />
          <path
            d="M304 335.79l-90.66-90.49a32 32 0 00-43.87-1.3L48 352M224 432l123.34-123.34a32 32 0 0143.11-2L464 368"
            fill="none"
            stroke="currentColor"
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="32"
          />
        </svg>
        <span>There is no images</span>
      </div>
    </template>
    <template id="img_items">
      <div
        :class="[
          'img_items border_box flex flex_wrap align_start',
          mode === 'list' ? 'list' : '',
        ]"
        ref="img_items"
        v-if="current_data.length"
      >
        <div
          :class="[
            'img_item_wrap flex flex_column relative',
            is_mobile ? 'mobile' : '',
          ]"
          v-for="(item, index) in current_data"
          :key="index"
          @click="onImgItem"
        >
          <button
            class="img_item"
            :style="item.download_url | getStyles"
            data-type="img"
            :data-index="index"
          ></button>
          <span
            class="options w_100 border_box flex justify_center align_center absolute bottom_0"
            data-type="url"
            :data-index="index"
            >{{ item.name }}</span
          >
        </div>
      </div>
    </template>

    <template id="pagination">
      <transition name="up">
        <div
          class="pagination_wrap w_100 border_box flex justify_center fixed z_index_100"
          v-show="visible_nav"
        >
          <div class="pagination flex justify_center align_center">
            <select id="select_page_size" name="page_size" v-model="page_size">
              <option
                v-for="(item, index) in array_page_size"
                :key="index"
                :value="item"
                >{{ item !== 99999999 ? item + " /page" : "all" }}</option
              >
            </select>
            <button
              class="btn_option prev"
              @click="onPrev"
              :disabled="Number(page) - 1 <= 0"
            >
              prev
            </button>
            <div class="tabbar_items flex align_center" @click="onPageItem">
              <button
                class="tabbar_item"
                :data-page="Number(page) - 4"
                v-if="Number(page) - 4 > 0"
              >
                {{ Number(page) - 4 }}
              </button>
              <button
                class="tabbar_item"
                :data-page="Number(page) - 3"
                v-if="Number(page) - 3 > 0"
              >
                {{ Number(page) - 3 }}
              </button>
              <button
                class="tabbar_item"
                :data-page="Number(page) - 2"
                v-if="Number(page) - 2 > 0"
              >
                {{ Number(page) - 2 }}
              </button>
              <button
                class="tabbar_item"
                :data-page="Number(page) - 1"
                v-if="Number(page) - 1 > 0"
              >
                {{ Number(page) - 1 }}
              </button>
              <button class="tabbar_item active" :data-page="Number(page)">
                {{ Number(page) }}
              </button>
              <button
                class="tabbar_item"
                :data-page="Number(page) + 1"
                :disabled="Number(page) + 1 > chunk_data.length"
              >
                {{ Number(page) + 1 }}
              </button>
              <button
                class="tabbar_item"
                :data-page="Number(page) + 2"
                :disabled="Number(page) + 2 > chunk_data.length"
              >
                {{ Number(page) + 2 }}
              </button>
              <button
                class="tabbar_item"
                :data-page="Number(page) + 3"
                :disabled="Number(page) + 3 > chunk_data.length"
              >
                {{ Number(page) + 3 }}
              </button>
              <button
                class="tabbar_item"
                :data-page="Number(page) + 4"
                :disabled="Number(page) + 4 > chunk_data.length"
              >
                {{ Number(page) + 4 }}
              </button>
              <button
                class="tabbar_item"
                :data-page="Number(page) + 5"
                v-if="page < 5"
                :disabled="Number(page) + 5 > chunk_data.length"
              >
                {{ Number(page) + 5 }}
              </button>
              <button
                class="tabbar_item"
                :data-page="Number(page) + 6"
                v-if="page < 4"
                :disabled="Number(page) + 6 > chunk_data.length"
              >
                {{ Number(page) + 6 }}
              </button>
              <button
                class="tabbar_item"
                :data-page="Number(page) + 7"
                v-if="page < 3"
                :disabled="Number(page) + 7 > chunk_data.length"
              >
                {{ Number(page) + 7 }}
              </button>
              <button
                class="tabbar_item"
                :data-page="Number(page) + 8"
                v-if="page < 2"
                :disabled="Number(page) + 8 > chunk_data.length"
              >
                {{ Number(page) + 8 }}
              </button>
            </div>
            <button
              class="btn_option next"
              @click="onNext"
              :disabled="Number(page) + 1 > chunk_data.length"
            >
              next
            </button>
            <input
              id="input_page"
              ref="input_page"
              type="number"
              placeholder="jump to"
              @keydown="onEnterPage"
            />
          </div>
        </div>
      </transition>
    </template>
  </div>
</template>
<script>
import Msg from "./msg";
import { chunk } from "@/utils/lodash.chunk";
import { clipboardCopy } from "@/utils/clipboard";
// const { chunk } = require("@/utils/lodash.chunk.js");
import { ImageBedUtils } from "@/utils/image-bed-utils";
import { githubApi } from "@/utils/github-api";
export default {
  components: {
    Msg,
  },
  data() {
    return {
      images_info: [],
      current: -1,
      style: "contain",
      page: 1,
      page_size: 10,
      array_page_size: [10, 20, 40, 80, 99999999],
      select_folder: [],
      current_folder: {},
      folder_prev: "",
      folder_use: "assets",
      img_data: [],
      chunk_data: [],
      current_data: [],
      visible_nav: true,
      visible_msg: false,
      visible_search: false,
      visible_folder_select: false,
      timer_msg: 0,
      msg: "",
      search_text: "",
      header_no_border: false,
      autoplay: false,
      timer_autoplay: 0,
      is_mobile: false,
      mode: "block",
    };
  },
  filters: {
    getStyles: function(path) {
      var str = "";

      str += ";background-image:url(" + path + ")";
      str += ";background-size:cover";
      str += ";background-position:center center";
      str += ";background-repeat:no-repeat";

      return str;
    },
  },

  mounted: function() {
    this.getDeviceInfo();
    // this.setLanding();
    // this.setChunkData();
    // this.getLocalStorage();
    this.getMediaFile();
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    document.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    download(url) {
      ImageBedUtils.downloadImageFromUrl(url);
    },
    async getMediaFile() {
      // this.$loading.show();
      const res = await githubApi.getFile(`db/images`);
      // this.$loading.hide();
      if (res) {
        this.img_data = res;
        this.chunk_data = chunk(this.img_data, this.page_size);
        this.current_data = this.chunk_data[this.page - 1];
      } else {
        this.$message.error(res.msg);
      }
    },
    preventScroll(event) {
      event.preventDefault();
    },
    getDeviceInfo: function() {
      if (document.body.offsetWidth <= 800) {
        this.is_mobile = true;
      } else {
        this.is_mobile = false;
      }
    },
    handleScroll: function() {
      const scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      const top = this.is_mobile ? 0 : this.$refs.img_items.offsetTop;

      if (scrollTop > top) {
        this.header_no_border = true;
      } else {
        this.header_no_border = false;
      }
    },
    setLanding: function() {
      setTimeout(() => {
        const landing_wrap = document.querySelector("#landing_wrap");
        const app = document.querySelector("#app");

        landing_wrap.style.opacity = 0;

        setTimeout(() => (landing_wrap.style.display = "none"), 300);

        app.style.opacity = 1;
      }, 300);
    },
    getImgs: function(children, arr) {
      children.map((item) => {
        if (item.type === "file") {
          arr.push(item);
        } else {
          if (item.children) {
            this.getImgs(item.children, arr);
          }
        }
      });
    },
    getLocalStorage: function() {
      const page_size = localStorage.getItem("page_size");
      const style = localStorage.getItem("style");
      const mode = localStorage.getItem("mode");

      if (page_size) this.page_size = page_size;
      if (style) this.style = style;
      if (mode) this.mode = mode;
    },
    onImgItem: function(e) {
      const _that = this;
      const type = e.target.dataset.type;
      const path = e.target.dataset.path;
      const index = Number(e.target.dataset.index);
      console.log(type);
      if (!type) {
        this.current = -1;
        this.autoplay = false;

        clearInterval(this.timer_autoplay);

        return;
      }

      function showMsg() {
        _that.msg = "link has copy to clipboard";
        _that.visible_msg = true;

        clearTimeout(_that.timer_msg);

        _that.timer_msg = setTimeout(() => {
          _that.visible_msg = false;
        }, 1200);
      }

      switch (type) {
        case "img":
          this.current = index;

          break;
        case "url":
          showMsg();

          clipboardCopy(this.current_data[index].path);
          break;
        case "path":
          showMsg();

          clipboardCopy(path);

          break;
        default:
          break;
      }
    },
    onChooseStyle: function(e) {
      const style = e.target.dataset.style;

      if (!style) return;

      this.style = style;

      localStorage.setItem("style", style);
    },
    onToggleAutoPlay: function() {
      this.autoplay = !this.autoplay;

      if (this.autoplay) {
        this.timer_autoplay = setInterval(() => {
          const target_next = this.current + 1;

          if (target_next < this.current_data.length) {
            this.current = target_next;
          } else {
            this.autoplay = false;
            clearInterval(this.timer_autoplay);
          }
        }, 2400);
      } else {
        clearInterval(this.timer_autoplay);
      }
    },
    onChangeCurrent: function(e) {
      const type = e.target.dataset.type;

      if (!type) return;

      switch (type) {
        case "prev":
          const target_prev = this.current - 1;

          if (target_prev >= 0) this.current = target_prev;

          break;
        case "next":
          const target_next = this.current + 1;

          if (target_next < this.current_data.length)
            this.current = target_next;

          break;
        default:
          break;
      }
    },
    onPageItem: function(e) {
      const page = e.target.dataset.page;

      if (!page) return;

      this.page = Number(page);
    },
    onPrev: function() {
      const target_prev = this.page - 1;

      if (target_prev > 0) this.page = target_prev;
    },
    onNext: function() {
      const target_next = this.page + 1;

      if (target_next <= this.chunk_data.length) this.page = target_next;
    },
    onEnterPage: function(e) {
      if (e.keyCode !== 13) return;

      const value = this.$refs.input_page.value;

      if (value <= 0 || value > this.chunk_data.length) return;

      this.page = value;
    },
    onToggleSearch: function() {
      this.visible_search = !this.visible_search;
      this.search_text = "";
    },
    onToggleFolder: function() {
      this.visible_folder_select = !this.visible_folder_select;
    },
    onToggleMode: function() {
      localStorage.setItem("mode", this.mode === "block" ? "list" : "block");

      if (this.mode === "block") return (this.mode = "list");
      if (this.mode === "list") return (this.mode = "block");
    },
  },
  watch: {
    page: function(new_val) {
      this.current_data = this.chunk_data[new_val - 1];
    },
    page_size: function(new_val) {
      this.chunk_data = chunk(this.img_data, new_val);
      this.current_data = this.chunk_data[0];
      this.page = 1;

      localStorage.setItem("page_size", Number(new_val));
    },
  },
};
</script>

<style lang="scss" scoped>
.empty_wrap {
  width: 100%;
  height: calc(100vh - 75px - 72px);
  opacity: 0.15;

  span {
    margin-top: 12px;
  }
}
.detail_wrap {
  background-color: rgba(0, 0, 0, 0.72);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);

  .detail {
    width: var(--size_w_detail);
    background: linear-gradient(-30deg, gray, white);
    border-radius: 6px;
    overflow: hidden;

    &.contain {
      .img_item {
        background-size: contain !important;
      }
    }

    &.cover {
      .img_item {
        background-size: cover !important;
      }
    }

    .img_item {
      height: var(--size_detail);

      .btn_close {
        top: 18px;
        right: 18px;
        padding: 1px;
        border-radius: 36px;
        cursor: pointer;
        transition: all ease 0.3s;
        border: 1px solid #eee;
        background-color: white;

        svg {
          width: 18px;
          height: 18px;
          opacity: 0.72;
        }
      }

      .btn_download {
        bottom: 18px;
        right: 224px;
        width: 26px;
        height: 26px;
        border-radius: 100px;
        font-size: 12px;
        cursor: pointer;
        background-color: rgba(255, 255, 255, 0.8);
        backdrop-filter: blur(10px);
        -webkit-backdrop-filter: blur(10px);
        border: 1px solid #eee;
        color: black;

        &:hover {
          box-shadow: 0 0 30px #ccc;
        }

        &:active {
          background-color: black;
          color: white;
        }
      }

      .btn_autoplay {
        bottom: 18px;
        right: 140px;
        width: 72px;
        padding: 5px 0;
        font-size: 12px;
        cursor: pointer;
        background-color: rgba(255, 255, 255, 0.8);
        backdrop-filter: blur(10px);
        -webkit-backdrop-filter: blur(10px);
        border: 1px solid #eee;
        border-radius: 100px;

        &:hover {
          box-shadow: 0 0 30px #ccc;
        }

        &:active,
        &.playing {
          background-color: black;
          color: white;
        }
      }

      .detail_options {
        bottom: 18px;
        border-radius: 100px;
        background-color: rgba(255, 255, 255, 0.8);
        backdrop-filter: blur(10px);
        -webkit-backdrop-filter: blur(10px);
        border: 1px solid #eee;
        overflow: hidden;
        transition: all ease 0.3s;

        &:hover {
          box-shadow: 0 0 30px #ccc;
        }

        &.style {
          left: 18px;
        }

        &.current {
          right: 18px;

          button {
            &:active {
              background-color: black;
              color: white;
            }

            &:first-child {
              border-right: 1px solid #eee;
            }
          }
        }

        button {
          width: 54px;
          padding: 5px 0;
          font-size: 12px;
          cursor: pointer;

          &.active {
            background-color: black;
            color: white;
            border-radius: 100px;
          }
        }
      }
    }
  }
}
.img_items {
  width: 80vw;
  padding: 12vh 0;
  margin: 0 auto;
  &.list {
    flex-direction: column;

    .img_item_wrap {
      width: 100%;
      margin-bottom: 24px;
      border-radius: 18px;
      overflow: hidden;

      &:hover,
      &.mobile {
        .img_item {
          transform: scale(1.08);
        }

        .options {
          transform: translateY(100%);
        }
      }

      .img_item {
        height: 27vh;
      }

      .detail_info {
        display: flex;
        top: 0;
        left: 0;
        width: 300px;
        height: 100%;
        padding: 15px;
        border-radius: 4px;
        background: linear-gradient(to left, transparent, rgba(0, 0, 0, 0.6));
        backdrop-filter: blur(15px);
        -webkit-backdrop-filter: blur(15px);
        color: white;
        cursor: pointer;
        border-radius: 18px;

        .name {
          font-size: 36px;
          font-weight: bold;
        }

        .detail {
          .detail_item {
            font-size: 15px;
            margin-bottom: 9px;

            &:last-child {
              margin-bottom: 0;
            }

            .title {
              width: 64px;
            }
          }
        }
      }
    }
  }

  .img_item_wrap {
    width: var(--size_img);
    height: auto;
    box-sizing: border-box;
    overflow: hidden;
    transition: all ease 0.3s;
    background: linear-gradient(-30deg, black, whitesmoke);

    &:hover,
    &.mobile {
      .options {
        transform: translateY(0);
      }
    }

    .img_item {
      width: 100%;
      height: var(--size_img);
      box-sizing: border-box;
      overflow: hidden;
      cursor: pointer;
      transition: all ease 0.3s;
    }

    .options {
      width: 100%;
      height: 36px;
      white-space: pre-wrap;
      font-size: 10px;
      overflow: hidden;
      cursor: pointer;
      background-color: rgba(0, 0, 0, 0.45);
      backdrop-filter: blur(3px);
      -webkit-backdrop-filter: blur(3px);
      color: white;
      transition: all ease 0.3s;
      transform: translateY(100%);
    }

    .detail_info {
      display: none;
    }
  }
}

/* -----pagination_wrap----- */
.pagination_wrap {
  bottom: 0;
}
.pagination_wrap .pagination {
  padding: 18px 24px;
  background-color: rgba(248, 249, 253, 0.72);
  backdrop-filter: blur(9px);
  -webkit-backdrop-filter: blur(9px);
  color: black;
}
.pagination_wrap .pagination #select_page_size {
  padding: 7px 9px;
  margin-right: 24px;
  box-sizing: border-box;
  border-radius: 4px;
  transition: all ease 0.3s;
  cursor: pointer;
  background-color: transparent;
  color: black;
  border: 1px solid rgba(0, 0, 0, 0.05);
  outline: none;
}
.pagination_wrap .pagination .btn_option {
  padding: 9px 18px;
  border-radius: 4px;
  transition: all ease 0.3s;
  cursor: pointer;
  background-color: black;
  color: white;
  border: 1px solid transparent;
}
.pagination_wrap .pagination .btn_option:hover {
  background-color: transparent;
  color: black;
  border: 1px solid black;
}
.pagination_wrap .pagination .btn_option:disabled {
  opacity: 0.1;
}
.pagination_wrap .pagination .tabbar_items {
  margin: 0 12px;
}
.pagination_wrap .pagination .tabbar_items .tabbar_item {
  width: 36px;
  height: 36px;
  margin: 0 4px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.05);
  transition: all ease 0.3s;
  cursor: pointer;
  font-size: 15px;
  line-height: 1;
}
.pagination_wrap .pagination .tabbar_items .tabbar_item:hover,
.pagination_wrap .pagination .tabbar_items .tabbar_item.active {
  border: 1px solid rgba(0, 0, 0, 0.6);
}
.pagination_wrap .pagination #input_page {
  width: 89px;
  padding: 9px 9px;
  margin-left: 24px;
  box-sizing: border-box;
  border-radius: 4px;
  transition: all ease 0.3s;
  cursor: pointer;
  background-color: transparent;
  color: black;
  border: 1px solid rgba(0, 0, 0, 0.05);
  outline: none;
}
.pagination_wrap .pagination #input_page::placeholder {
  color: #888;
}
</style>
